<template>
  <div class="login-page">
    <!--login-left start-->
    <div class="login-left">
      <div class="login-left-con">
        <div class="login-logo"><img src="../assets/images/login2.png"></div>
        <div class="login-en">Welcome</div>
        <div class="login-ch">欢迎登陆机满满管理后台</div>
      </div>
    </div>
    <!--login-left end-->
    <!--login-right start-->
    <div class="login-right">
      <div class="login-right-con">
        <div class="login-tit">忘记密码</div>
        <div class="login-ul">
          <div class="login-item"><input type="text" class="login-input" placeholder="请输入手机号" /> </div>
          <div class="login-item">
            <input type="text" class="login-input login-input-ver" placeholder="请输入验证码" />
            <button class="login-ver-btn">获取验证码</button>
          </div>
          <div class="login-item"><input type="text" class="login-input" placeholder="设置新密码" /> </div>
          <div class="login-item"><input type="text" class="login-input" placeholder="再次输入新密码" /> </div>
        </div>
        <div class="login-forget">
          <router-link class="login-forget-y" to="/">账号密码登录</router-link>
        </div>
        <div class="login-bot">
          <button class="login-btn">确认</button>
        </div>
      </div>
    </div>
    <!--login-right end-->
  </div>
</template>

<script>
export default {
  name: "login"
}
</script>

<style scoped>
@import "../assets/styles/login.css";
</style>



